export const updateObjectArray = (items, itemId, objPropName, newObjProps) => {
    return items.map(i =>{
        if (i[objPropName] === itemId){ //  id  === id   (action)
            //   followed    Users
            //  user( user   users)    
            return {...i, ...newObjProps}; 
    }
    return i;
});
}