import React from "react";
import { reduxForm, Field } from 'redux-form';
import { Input, createField } from '../common/FormsControls/FormsControls';
import { maxLengthCreator, required } from '../../utils/validators/validators';
import { connect } from "react-redux";
import { login } from '../../redux/auth-reducer';
import { Navigate } from "react-router-dom";
import s from '../common/FormsControls/FormsControls.module.css';



const maxLength30 = maxLengthCreator(30);

const LoginForm = ({ handleSubmit, error }) => {

    return (
        //       onChage      Field
        //    onChange   

        //      JS   onSubmit.    submit  
        // handleSubmit    reduxForm.
        //handleSubmit -  1. e.preventDefault. 2 get all form data and put them to object.
        // 3. reduxForm  onSubmit(login{formData})         
        //handleSubmit
        <form onSubmit={handleSubmit}>

            {createField("Email", "email", [required, maxLength30], Input)}
            {/* <Field validate={[required, maxLength30]} placeholder={'Email'} name={'email'} component={Input} /> */}
            {createField("Password", 'password', [required, maxLength30], Input, "password")}
            {createField(null, 'rememberMe', [], Input, "checkbox")}remember Me

            {error && <div className={s.formSummaryError}>
                {error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login' //  connect       
    //  . 
    //  mapStateToProps, mapStateToProps  ,     
    //     form: 'login'    reduxForm
})(LoginForm)


const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }
    //onSubmit={onSubmit}    LoginReduxForm   onSubmit,  handleSubmit  formData
    //
    if (props.isAuth) {
        return <Navigate to={'/profile'} />
    }
    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit} />
    </div>
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { login })(Login);